@import 'vars';
@import 'mixins';

.#{$prefix}pi__ {
  position: relative;

  &table-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex: 1;

    box-sizing: border-box;

    @include mobile {
      display: block;
      padding: 0 10px;
      background: linear-gradient(to bottom, #f4f5fa, #e9eeff); } }



  &header {
    height: 85px;
    background-color: #f4f4ff;
    padding: 16px $small-padding 4px;

    @include large-desktop {
      padding: 8px $large-padding 4px; }


    @include mobile {
      position: relative;
      padding-left: 0;
      padding-right: 0;
      height: auto;

      &:after {
        display: block;
        content: "";
        height: 1px;
        width: 100%;
        background: #ddd;
        bottom: 0; } } }

  &footer {
    width: 100%;
    padding: 36px 0 48px;
    border-top: 1px solid #ccc;
    margin: 40px auto 0;

    @include mobile {
      margin-top: 40px;
      padding: 0;
      border-top: none; } }

  &row-header {
    box-sizing: border-box;
    display: flex;
    table-layout: fixed;
    margin: 0 auto;

    @include mobile {
      flex-direction: column;
      margin-top: 50px;
      padding: 0 14px; } }

  &cell-header1 {
    position: relative;
    display: table-cell;
    flex: 1;
    flex-direction: column;
    padding-top: 50px;

    @include mobile {
      display: none; } }

  &cell-header2 {
    position: relative;
    right: 7%;
    display: table-cell;
    flex: 1;
    flex-direction: column;
    text-align: right;
    padding-top: 50px;

    @include mobile {
      display: none; } }

  &cell-header3 {
    display: flex;
    flex: 1;
    flex-direction: column;
    flex-wrap: no-wrap;
    border: 1px solid black;
    height: 100%; }

  &cell-header4 {
    display: flex;
    flex: 1;
    flex-direction: column; }

  &am-container {
    display: flex;
    flex: 1;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;

    @include mobile {
      margin-bottom: 24px; } }

  &am-header-row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-end;

    &--disabled {
      opacity: 0.2;
      .#{$prefix}pi__btn, .#{$prefix}input--stock,
      .#{$prefix}btn--primary--am {
        cursor: not-allowed; } }

    @include mobile {

      .#{$prefix}input--stock {
        width: 49px; }

      .#{$prefix}pi__am-item {
        flex: 1;
        text-align: center;
        text-transform: uppercase;
        height: 34px;
        border-radius: 3px;
        position: relative; }

      .#{$prefix}pi__btn-am-font {
        left: 0;
        top: 4px;
        font-size: 13px; } } }

  &am-row-item {
    display: flex;
    flex-direction: row;
    align-items: center;

    .#{$prefix}copy {
      font-size: 12px;
      font-weight: 500;

      @include mobile {
        font-size: 14px;
        font-weight: 300; } }

    .#{$prefix}copy {
      vertical-align: bottom; }

    @include mobile {
      flex: 1;
      justify-content: flex-start;

      .#{$prefix}pi__btn {
        width: 34px;
        height: 34px;
        margin: 0; }

      &--lead {
        .#{$prefix}pi__btn {
          display: none; } } } }

  &rapid_icon {
    position: relative;
    // top: 3px
    // left: 16px
    width: 30px;
    height: 25px;
    object-fit: contain;

    @include mobile {
      position: absolute;
      right: 10px;
      top: 5px; } }

  &am-field {
    width: 44px;
    height: 28px;
    border-radius: 3px;
    box-sizing: border-box;
    background-color: #ffffff;
    border: solid 1px #dddddd;
    text-align: center;
    color: $dark-grey;
    font-size: 12px;

    @include mobile {
      margin-left: 10px;
      margin-right: 10px;
      width: 49px;
      height: 34px;
      font-size: 14px; }

    &--lead {
      @include mobile {
        margin-left: 0;
        margin-right: 0; } } }

  &row-container {
    margin: 0 auto;
    flex: 1;
    overflow: auto;
    padding: 0 $small-padding;
    @include large-desktop {
      padding: 0 $large-padding; }

    @include mobile {
      width: 100%;
      padding-left: 0;
      padding-right: 0; } }

  &row {
    box-sizing: border-box;
    display: table;
    table-layout: fixed;
    width: 100%;
    height: 60px;
    border-bottom: 1px solid #ddd;

    @include mobile {
      display: flex;
      flex-direction: column;
      height: auto;
      background: #fff;
      border: 1px solid #ddd;
      box-shadow: 0 2px 1px 0 rgba(0, 0, 0, 0.12);
      margin-bottom: 15px;
      padding: 15px; }

    &-title {
      display: none;
      @include mobile {
        display: block;
        margin: 10px 0; } }

    .#{$prefix}pi__btn {
      @include mobile {
        display: none; } } }

  &cell1 {
    display: table-cell;
    flex: 1;
    flex-direction: column;
    position: relative;
    vertical-align: middle;

    @include mobile {
      border-bottom: 1px solid #ddd;
      margin-bottom: 5px; } }

  &cell2 {
    position: relative;
    right: 7%;
    display: table-cell;
    flex: 1;
    flex-direction: column;
    text-align: center;
    vertical-align: middle;
    text-align: right;

    @include mobile {
      display: none; } }

  &price-container {
    justify-content: flex-end;
    &--markup {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      align-items: center;
      height: 100%;

      @include mobile {
        flex: 1; } }

    &--lead {
      display: table-cell;
      flex: 1;
      flex-direction: column;
      position: relative;
      vertical-align: middle;
      text-align: right;

      .#{$prefix}copy--product-import {
        margin-right: 3px; } }

    &--disabled {
      .#{$prefix}pi__btn, .#{$prefix}input--stock {
        opacity: 0.2;
        cursor: not-allowed; } }


    @include mobile {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      margin-top: 10px; } }

  &mark-up-item {
    position: relative; }

  &btn {
    width: 28px;
    height: 28px;
    margin: 0;
    cursor: pointer;
    color: #73DAC7;

    &:hover {
      color: $primary-hover; } }

  &sub-field {
    width: 40px;
    height: 28px;
    border-radius: 3px;
    box-sizing: border-box;
    background-color: #ffffff;
    border: solid 1px #dddddd;
    text-align: center;
    color: $dark-grey;
    font-size: 12px;

    @include mobile {
      opacity: 1;
      width: 60px;
      height: 34px;
      font-size: 14px; } }

  &apply-all-btn {
    width: 240px;
    box-sizing: border-box;
    margin: 0 auto;
    padding-top: 8px;
    padding-bottom: 8px; } }


.#{$prefix}product-import-status {
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-bottom: 9px;

  .#{$prefix}product-import__indicator {
    display: inline-block;
    background: #ddd;
    border-radius: 50%;
    width: 22px;
    height: 22px;
    margin-right: 10px;

    .#{$prefix}svg-tick {
      width: 10px;
      height: 7px;

      fill: #fff;
      display: none; } }

  &--complete {
    .#{$prefix}product-import__indicator {
      display: flex;
      align-items: center;
      justify-content: center;
      background: $primary;

      .#{$prefix}svg-tick {
        display: block; } } } }

@media (max-width: 539px) {
  .pts-pi--header {
    height: 120px; }

  .pts-pi--am-header-row {
    justify-content: center; } }


@media (max-width: 700px) {
  .pts-pi--mark-up-container {
    flex-direction: column; }

  .pts-pi--btn-fade {
    margin: 5px 0;
    cursor: pointer; } }
